.wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top:  20px;
  background: linear-gradient(45deg, #aaa, #2c5);
  border-radius: 10px;
}
h1 {
  text-align: center;
  width: 80%;
}
.area {
  background: #fff;
}
.textarea {
  width: 400px;
  height: 500px;
  padding: 0;
  border: 2px red solid;

}
.textarea textarea {
  width: 100%;
  height: 100%;
}
.preview {
  display: flex;
  width: 400px;
  height: 500px;
  border: 2px blue solid;
  padding: 10px;
  overflow-x: hidden;
}
pre {
  overflow: visible;
}
